html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

a { text-decoration: none; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

@font-face { font-family: "Harmonia Sans Std"; font-style: normal; font-weight: 900; src: local("Harmonia Sans Std Black"), local("HarmoniaSansStd-Black"), url("../fonts/HarmoniaSansStd-Black.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-Black.woff") format("woff"); }

@font-face { font-family: "Harmonia Sans Std"; font-style: italic; font-weight: 900; src: local("Harmonia Sans Std Black Italic"), local("HarmoniaSansStd-BlackItalic"), url("../fonts/HarmoniaSansStd-BlackItalic.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-BlackItalic.woff") format("woff"); }

@font-face { font-family: "Harmonia Sans Std"; font-style: normal; font-weight: 700; src: local("Harmonia Sans Std Bold"), local("HarmoniaSansStd-Black"), url("../fonts/HarmoniaSansStd-Bold.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-Bold.woff") format("woff"); }

@font-face { font-family: "Harmonia Sans Std"; font-style: italic; font-weight: 700; src: local("Harmonia Sans Std Bold Italic"), local("HarmoniaSansStd-BoldItalic"), url("../fonts/HarmoniaSansStd-BoldItalic.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-BoldItalic.woff") format("woff"); }

@font-face { font-family: "Harmonia Sans Std"; font-style: normal; font-weight: 400; src: local("Harmonia Sans Std"), local("HarmoniaSansStd-Regular"), url("../fonts/HarmoniaSansStd-Regular.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-Regular.woff") format("woff"); }

@font-face { font-family: "Harmonia Sans Std"; font-style: italic; font-weight: 400; src: local("Harmonia Sans Std Italic"), local("HarmoniaSansStd-Italic"), url("../fonts/HarmoniaSansStd-Italic.woff2") format("woff2"), url("../fonts/HarmoniaSansStd-Italic.woff") format("woff"); }

@font-face { font-family: "Roboto", Arial, sans-serif; font-style: normal; font-weight: 400; src: local("Roboto"), local("Roboto-Regular"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

@font-face { font-family: "Roboto", Arial, sans-serif; font-style: italic; font-weight: 400; src: local("Roboto Italic"), local("Roboto-Italic"), url("../fonts/Roboto-Italic.woff2") format("woff2"), url("../fonts/Roboto-Italic.woff") format("woff"); }

.icon-arrow-down { width: 1.5em; height: 1em; fill: currentColor; }

.icon-facebook { width: 0.53em; height: 1em; fill: currentColor; }

body { font-family: "Roboto", Arial, sans-serif, Arial, sans-serif; font-size: 16px; font-weight: 400; line-height: 2.375; color: rgba(0, 0, 0, 0.6); -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; opacity: 0; visibility: hidden; transition: opacity .25s ease-in-out, visibility .25s ease-in-out; }

body.is-loaded { opacity: 1; visibility: visible; }

.color-red { color: #D65151; }

.title { font-family: "Harmonia Sans Std", "Open Sans", sans-serif; font-size: 14px; font-weight: 700; line-height: 1.57; text-transform: uppercase; }

.row { margin: 0px -15px; display: flex; flex-wrap: wrap; }

.col { padding: 0 15px; }

.col-1 { flex: 0 0 8.33333%; max-width: 8.33333%; }

.col-2 { flex: 0 0 16.66667%; max-width: 16.66667%; }

.col-3 { flex: 0 0 25%; max-width: 25%; }

.col-4 { flex: 0 0 33.33333%; max-width: 33.33333%; }

.col-5 { flex: 0 0 41.66667%; max-width: 41.66667%; }

.col-6 { flex: 0 0 50%; max-width: 50%; }

.col-7 { flex: 0 0 58.33333%; max-width: 58.33333%; }

.col-8 { flex: 0 0 66.66667%; max-width: 66.66667%; }

.col-9 { flex: 0 0 75%; max-width: 75%; }

.col-10 { flex: 0 0 83.33333%; max-width: 83.33333%; }

.col-11 { flex: 0 0 91.66667%; max-width: 91.66667%; }

.col-12 { flex: 0 0 100%; max-width: 100%; }

.inner { margin: 0 auto; padding: 0 30px; max-width: 1280px; }

.header { position: absolute; top: 0; left: 0; z-index: 3; width: 100%; padding-top: 40px; }

.header__inner { padding: 0 70px; margin: 0 auto; max-width: 1920px; flex-wrap: wrap; justify-content: space-between; align-items: center; display: flex; }

.header__nav { padding: 0 20px; }

.header__login { flex-wrap: wrap; justify-content: center; align-items: center; }

.header__link { margin-right: 33px; font-family: "Harmonia Sans Std", "Open Sans", sans-serif; color: #fff; transition: color .3s; font-size: 18px; font-weight: 700; line-height: 50px; }

.no-touch .header__link:hover { color: #D65151; }

.logo img { display: block; }

.logo__text { display: none; }

.nav { font-family: "Harmonia Sans Std", "Open Sans", sans-serif; font-size: 18px; font-weight: 400; line-height: 2.22; }

.nav__list { align-items: center; flex-wrap: wrap; display: flex; }

.nav__item:not(:first-child) { margin-left: 53px; }

.nav__link { position: relative; color: #fff; opacity: 0.5; transition: opacity .3s; padding-bottom: 7px; display: block; }

.no-touch .nav__link:hover { opacity: 1; }

.nav__link.is-active { opacity: 1; }

.nav__link.is-active:before { position: absolute; content: ''; left: 0; bottom: 0; width: 100%; height: 2px; background-color: #D65151; }

.btn { padding: 14px 30px 12px; font-family: "Harmonia Sans Std", "Open Sans", sans-serif; color: #fff; transition: background-color .3s; font-size: 18px; font-weight: 700; line-height: 1.3; border-radius: 5px; vertical-align: top; background-color: #D65151; display: inline-block; }

.no-touch .btn:hover { background-color: #c52f2f; }

.btn-circle { height: 60px; width: 60px; border-radius: 50%; text-align: center; line-height: 60px; transition: background-color .3s; font-size: 0; background-color: #fff; box-shadow: 0 20px 30px 0 rgba(42, 57, 65, 0.1); vertical-align: top; display: inline-block; }

.no-touch .btn-circle:hover { background-color: #D65151; }

.no-touch .btn-circle:hover .icon { color: #fff; }

.btn-circle .icon { font-size: 8px; color: #D65151; transition: color .3s; vertical-align: middle; line-height: 1; display: inline-block; }

.hero { position: relative; padding: 170px 0 235px; height: 100vh; min-height: 630px; align-items: center; justify-content: center; display: flex; flex-direction: column; }

.hero .btn-circle { position: absolute; left: 50%; bottom: 40px; z-index: 4; margin-left: -30px; transform: translateY(-30px); opacity: 0; }

.hero__back-img { position: absolute; top: 0; left: 0; width: 100%; object-fit: cover; object-position: center; pointer-events: none; height: 100%; }

.hero__front-img { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 100%; max-width: 1918px; z-index: 1; }

.hero__topseparator { position: relative; align-items: center; justify-content: center; display: flex; margin-bottom: 40px; }

.hero__topseparator img { opacity: 0; margin: 0 50px; max-width: 100%; display: block; }

.hero__top-line-left, .hero__top-line-right { margin-bottom: 10px; width: 182px; height: 1px; transform: scaleX(0); background-color: #fff; }

.hero__top-line-left { transform-origin: right; }

.hero__top-line-right { transform-origin: left; }

.hero__bottomseparator { position: relative; z-index: 1; margin-top: 28px; max-width: 500%; width: 100%; height: 1px; transform: scaleX(0); background-color: #fff; }

.hero__title { position: relative; z-index: 1; font-family: "Harmonia Sans Std", "Open Sans", sans-serif; font-size: 140px; font-weight: 900; line-height: 145px; color: #fff; letter-spacing: 10px; transition: text-shadow 0.5s ease-in-out; }

.hero__title.is-animate { overflow: visible; text-shadow: 0 30px 30px rgba(42, 57, 65, 0.8); }

.hero__title_red { margin-left: 0.3%; color: #D65151; }

.hero__title_red.is-animate { text-shadow: 0 40px 40px rgba(42, 57, 65, 0.2); }

[data-anim] { position: relative; }

[data-anim-text-parent] { overflow: hidden; display: block; }

[data-anim="text-from-bottom"] { display: block; transform: translateY(102%); }

[data-anim="from-bottom"] { transform: translateY(100px); opacity: 0; }

.about { padding: 203px 0 120px; font-family: "Harmonia Sans Std", "Open Sans", sans-serif; font-size: 28px; font-weight: 400; line-height: 1.928; text-align: center; background-repeat: no-repeat; background-position: center 116px; }

.about__title { margin-bottom: 35px; }

.about__footer { margin-top: 40px; }
