//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }




$harmoniaSansStd: 'Harmonia Sans Std';
@mixin hss {
	font-family: $harmoniaSansStd, 'Open Sans', sans-serif; }

@font-face {
	@include fontdefault(normal, 900, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std Black', 'HarmoniaSansStd-Black', HarmoniaSansStd-Black); }
@font-face {
	@include fontdefault(italic, 900, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std Black Italic', 'HarmoniaSansStd-BlackItalic', HarmoniaSansStd-BlackItalic); }

@font-face {
	@include fontdefault(normal, 700, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std Bold', 'HarmoniaSansStd-Black', HarmoniaSansStd-Bold); }
@font-face {
	@include fontdefault(italic, 700, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std Bold Italic', 'HarmoniaSansStd-BoldItalic', HarmoniaSansStd-BoldItalic); }

@font-face {
	@include fontdefault(normal, 400, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std', 'HarmoniaSansStd-Regular', HarmoniaSansStd-Regular); }
@font-face {
	@include fontdefault(italic, 400, $harmoniaSansStd);
	@include fontpath('Harmonia Sans Std Italic', 'HarmoniaSansStd-Italic', HarmoniaSansStd-Italic); }



$roboto: 'Roboto', Arial, sans-serif;
@mixin rob {
	font-family: $roboto, Arial, sans-serif; }
@font-face {
	@include fontdefault(normal, 400, $roboto);
	@include fontpath('Roboto', 'Roboto-Regular', Roboto-Regular); }
@font-face {
	@include fontdefault(italic, 400, $roboto);
	@include fontpath('Roboto Italic', 'Roboto-Italic', Roboto-Italic); }
